// 登录界面
<template>
    <div class="login-box">
        <div class="title">知晓</div>

        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px">
            <el-form-item label="用户名 ：" prop="name">
                <el-input v-model="ruleForm.name" clearable></el-input>
            </el-form-item>

            <el-form-item label="密码 ：" prop="password">
                <el-input v-model="ruleForm.password" show-password></el-input>
            </el-form-item>
            <el-form-item class="login-btn">
                <el-button type="primary" @click="submitForm('ruleForm')">登录</el-button>
                <el-button @click="resetForm('ruleForm')">重置</el-button>
            </el-form-item>
        </el-form>
        <div style="margin: 20px 0">
            <span class="login-register" @click="gotoRegister()">
                <el-button type="primary" plain>
                    <i class="el-icon-arrow-left"></i>
                    注册
                </el-button>
            </span>
            <span class="login-forget" @click="gotoForget()">
                <el-button plain>
                    <i class="el-icon-arrow-right"></i>
                    忘记密码
                </el-button>
            </span>
        </div>
    </div>
</template>

<script>
import { Login } from '../services/login'
export default {
    data () {
        return {
            ruleForm: {
                name: "",
                password: ""
            },
            rules: {
                name: [
                    {
                        required: true,
                        message: "请输入用户名",
                        trigger: "blur"
                    },
                    {
                        min: 3,
                        max: 10,
                        message: "长度在 3 到 10 个字符",
                        trigger: "blur"
                    }
                ],
                password: [
                    {
                        required: true,
                        message: "请输入密码",
                        trigger: "blur"
                    },
                    {
                        min: 2,
                        max: 20,
                        message: "长度在 5 到 20 个字符",
                        trigger: "blur"
                    }
                ]
            }
        };
    },
    methods: {
        // 登录验证
        submitForm (formName) {
            var that = this;
            this.$refs[formName].validate(valid => {
                if (valid) {
                    Login(this.ruleForm.name, this.ruleForm.password).then(res => {
                        // console.log(res);

                        if (res.data.code == '0') {
                            that.open1()
                            this.$router.push('/index');

                        } else {
                            that.open4()
                            // return false;
                        }
                    })
                } else {
                    return false;
                }
            });
        },
        // 注册成功执行的方法
        open1 () {
            this.$message({
                showClose: true,
                message: "登录成功！"
            });
        },
        open4 () {
            this.$message({
                showClose: true,
                message: "用户不存在/密码错误",
                type: "error"
            });
        },
        // 重置
        resetForm (formName) {
            this.$refs[formName].resetFields();
        },
        /*提交进行判断的函数 */
        // submit: function() {
        // 	if (this.form.name === "张三" && this.form.password === "123456")
        // 		alert("登陆成功");
        // 	else alert("账号或密码错误");
        // },
        // register: function() {}

        gotoRegister () {
            this.$router.replace("/register");
        },
        gotoForget () {
            this.$router.replace("/forget");
        }
    }
};
</script>
