// 注册界面
<template>
    <div class="register-box">
        <div class="title">知晓</div>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px">
            <el-form-item label="账号 ：" prop="name">
                <el-input v-model="ruleForm.name" clearable ref="name"></el-input>
            </el-form-item>

            <el-form-item label="手机号 ：" prop="number">
                <el-input v-model="ruleForm.number" clearable ref="number"></el-input>
            </el-form-item>

            <el-form-item label="密码 ：" prop="password">
                <el-input v-model="ruleForm.password" show-password ref="password"></el-input>
            </el-form-item>

            <el-form-item label="确认密码 ：" prop="password2">
                <el-input v-model="ruleForm.password2" show-password ref="password2"></el-input>
            </el-form-item>

            <el-form-item label="昵称 ：" prop="nickname">
                <el-input v-model="ruleForm.nickname" clearable ref="nickname"></el-input>
            </el-form-item>
            <el-form-item label="验证码 ：" prop="test">
                <el-input v-model="ruleForm.test" class="test-ipt" clearable ref="test"></el-input>
                <button @click="countDown" :class="{disabled:!this.flag}" class="test">{{message}}</button>
            </el-form-item>
            <el-form-item class="login-btn">
                <el-button type="primary" @click="submitForm('ruleForm')">注册</el-button>
                <el-button @click="resetForm('ruleForm')">重置</el-button>
            </el-form-item>
        </el-form>

        <div style="margin: 20px 0">
            <span class="login-register" @click="gotoLogin()">
                <el-button type="primary" plain>
                    <i class="el-icon-arrow-left"></i>
                    登录
                </el-button>
            </span>
            <span class="login-forget" @click="gotoForget()">
                <el-button plain>
                    <i class="el-icon-arrow-right"></i>
                    忘记密码
                </el-button>
            </span>
        </div>
    </div>
</template>

<script>
import { Register } from "../services/register";
import { Code } from "../services/code";
export default {
    data () {
        return {
            message: "获取验证码",
            time: "60",
            flag: true,

            ruleForm: {
                name: "",
                test: "",
                password: "",
                password2: "",
                number: "",
                nickname: ""
            },
            rules: {
                name: [
                    {
                        required: true,
                        message: "请输入用户名",
                        trigger: "blur"
                    },
                    {
                        min: 3,
                        max: 12,
                        message: "长度是3 到 12个字符",
                        trigger: "blur"
                    }
                ],
                test: [
                    {
                        required: true,
                        message: "请输入验证码",
                        trigger: "blur"
                    },
                    {
                        min: 1,
                        max: 6,
                        message: "长度在 1 到 6 个字符",
                        trigger: "blur"
                    }
                ],
                number: [
                    {
                        required: true,
                        message: "请输入手机号",
                        trigger: "blur"
                    },
                    {
                        min: 11,
                        max: 11,
                        message: "长度是11个字符",
                        trigger: "blur"
                    }
                ],
                password: [
                    {
                        required: true,
                        message: "请输入密码",
                        trigger: "blur"
                    },
                    {
                        min: 3,
                        max: 12,
                        message: "长度在 3 到 12 个字符",
                        trigger: "blur"
                    }
                ],
                password2: [
                    {
                        required: true,
                        message: "请再次输入密码",
                        trigger: "blur"
                    },
                    {
                        min: 3,
                        max: 12,
                        message: "长度在 3 到 12 个字符",
                        trigger: "blur"
                    }
                ],
                nickname: [
                    {
                        required: true,
                        message: "请输入昵称",
                        trigger: "blur"
                    },
                    {
                        min: 3,
                        max: 12,
                        message: "长度在 3 到 12 个字符",
                        trigger: "blur"
                    }
                ]
            }
        };
    },
    methods: {
        // 注册验证
        submitForm (formName) {
            var that = this;
            // 验证密码
            if (this.ruleForm.password != this.ruleForm.password2) {
                this.open4('两次密码不一致')
                return false;
            }


            this.$refs[formName].validate(valid => {
                if (valid) {
                    // 将注册填的信息提交到后台，保存在个人信息
                    // alert("submit!");
                    Register(
                        this.$refs.name.value,
                        this.$refs.test.value,
                        this.$refs.number.value,
                        this.$refs.password.value,
                        this.$refs.nickname.value
                    ).then(res => {
                        // console.log(res);
                        if (res.data.code == "0") {
                            // console.log("成功");
                            that.open1();
                            this.$router.push("/login");
                        } else {
                            // console.log(res.data.msg);
                            that.open4(res.data.msg)
                        }
                    });
                } else {
                    return false;
                }
            });
        },
        // 重置
        resetForm (formName) {
            this.$refs[formName].resetFields();
        },
        // 验证码倒计时的设置
        countDown () {
            Code(this.$refs.number.value);
            // 首先判断是否有计时器已经开了
            if (this.flag == false) {
                return;
            }
            this.flag = false;
            this.message = this.time + "s后重新发送"; //点击从60s倒计时开始而不是59s
            let clock = window.setInterval(() => {
                this.time--;
                this.message = this.time + "s后重新发送";
                if (this.time < 0) {
                    window.clearInterval(clock);
                    this.message = "重新发送验证码";
                    this.time = 60;
                    this.flag = true; //重新启动
                }
            }, 1000);
        },
        // 点击登录按钮后跳转
        gotoLogin () {
            this.$router.replace("/login");
        },
        // 点击忘记密码按钮后跳转
        gotoForget () {
            this.$router.replace("/forget");
        },
        // 注册成功执行的方法
        open1 () {
            this.$message({
                showClose: true,
                message: "注册成功！"
            });
        },
        // 注册失败时执行的方法
        open4 (msg) {
            this.$message({
                showClose: true,
                message: msg,
                type: "error"
            });
        }
    },
    watch: {

    }

};
</script>
<style lang='less'>
.register-box {
    /* 修改字体大小 */
    .el-form-item__label {
        font-size: 16px !important;
        padding: 0;
    }
    /* 注册 */
    .login-register {
        position: absolute;
        bottom: 10px;
        left: 20px;
    }
    /* 忘记密码 */
    .login-forget {
        position: absolute;
        bottom: 10px;
        right: 20px;
    }
    /* 居中设置 */
    .el-form {
        margin-top: 10%;
    }
    /* 输入框 */
    .el-input__inner {
        height: 35px !important;
        line-height: 35px !important;
        width: 90%;
    }
    /* 登录 重置按钮间距 */
    .el-button + .el-button {
        margin-left: 20px !important;
    }
    /* 标题 */
    .title {
        font-size: 36px;
        color: #409eff;
        text-align: center;
        margin-top: 30px;
    }
    /* 发送验证码 */
    .test {
        color: white;
        background-color: #409eff;
        border: 0;
        width: 110px;
        height: 40px;
        border-radius: 10px;
        outline: none;
        cursor: pointer;
        margin-left: 20px;
    }
    .disabled {
        background-color: #ddd;
        color: #57a3f3;
    }
    /* 输入验证码的框 */
    .test-ipt {
        width: 40% !important;
    }
}
</style>