<template>
    <div class="my-box">
        <div class="header" @click="getimg()" ref="header">
            <input type="file" ref="imgIpt" />
            <span>上传头像</span>
            <img v-show="flag" :src="touxiang" alt @click="changeAvatar()" ref="changeimg" />
        </div>
        <div class="main">
            <el-collapse v-model="activeNames">
                <el-collapse-item title="我的收藏" name="1">
                    <li
                        class="collect"
                        v-for="(item,index) in collectList"
                        :key="index"
                        @click="goDetail(item.articleId)"
                    >
                        <i class="el-icon-caret-right"></i>
                        {{item.title}}
                    </li>
                </el-collapse-item>
                <el-collapse-item title="查看个人信息" name="2">
                    <div class="my-message">
                        <span>昵&nbsp;&nbsp;&nbsp;称：</span>
                        {{mymessageObj.userName}}
                    </div>
                    <div class="my-message">
                        <span>用户名：</span>
                        {{mymessageObj.loginName}}
                    </div>
                    <div class="my-message">
                        <span>手机号：</span>
                        {{mymessageObj.phonenumber}}
                    </div>
                </el-collapse-item>
                <el-collapse-item title="修改个人信息" name="3">
                    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px">
                        <el-form-item label="昵称 ：" prop="nickname">
                            <el-input v-model="ruleForm.nickname" clearable ref="nickname"></el-input>
                        </el-form-item>

                        <el-form-item label="手机号 ：" prop="phonenumber">
                            <el-input v-model="ruleForm.phonenumber" ref="phonenumber"></el-input>
                        </el-form-item>
                        <el-form-item class="login-btn">
                            <el-button type="primary" plain @click="submitForm('ruleForm')">确认修改</el-button>
                        </el-form-item>
                    </el-form>
                </el-collapse-item>
            </el-collapse>

            <el-button
                @click="myClose()"
                type="primary"
                icon="el-icon-switch-button"
                class="my-close"
            >退出登录</el-button>
        </div>

        <!-- 退出登录按钮 -->
        <!-- <el-button
            @click="myClose()"
            type="primary"
            icon="el-icon-switch-button"
            class="my-close"
        >退出登录</el-button>-->
        <!-- 显示底部tabbar -->
        <mt-tabbar v-model="selected">
            <mt-tab-item id="index">
                <span slot="icon" class="iconfont icon-news_icon"></span>
                首页
            </mt-tab-item>

            <mt-tab-item id="music">
                <!-- <img slot="icon" src="../assets/100x100.png" /> -->
                <span slot="icon" class="iconfont icon-yinle"></span>
                音乐
            </mt-tab-item>

            <mt-tab-item id="my">
                <span slot="icon" class="iconfont icon-wode"></span>
                我的
            </mt-tab-item>
        </mt-tabbar>
    </div>
</template>
<script>
import { Mymessage, ChangMessage, SetAvatar, goOut } from "../services/my";
import { CollectList } from "../services/shoucang";
export default {
    data () {
        return {
            touxiang: '',
            selected: "my",
            activeNames: "",
            flag: true,
            mymessageObj: {},
            collectList: [],
            ruleForm: {
                nickname: "",
                phonenumber: ""
            },
            rules: {
                nickname: [
                    {
                        required: true,
                        message: "请输入昵称",
                        trigger: "blur"
                    },
                    {
                        min: 3,
                        max: 10,
                        message: "长度在 3 到 10 个字符",
                        trigger: "blur"
                    }
                ],
                phonenumber: [
                    {
                        required: true,
                        message: "请输入手机号",
                        trigger: "blur"
                    },
                    {
                        min: 11,
                        max: 11,
                        message: "长度为11个字符",
                        trigger: "blur"
                    }
                ]
            }
        };
    },
    methods: {
        // 本地上传头像
        getimg () {
            var that = this;
            this.$refs.imgIpt.onchange = function (e) {
                var file = e.target.files[0];
                SetAvatar(file).then(() => {
                    Mymessage().then(res => {
                        // console.log(res.data.data.avatar);
                        that.touxiang = res.data.data.avatar;
                    });
                });
            };
            this.flag = true;
        },
        // 点击已上传的头像再修改新头像的方法
        changeAvatar () {
            this.$refs.imgIpt.click();
        },
        // 点击进入收藏文章详情
        goDetail (articleId) {
            this.$router.push(`/detail/${articleId}`);
        },
        // 修改个人信息
        submitForm (formName) {
            var that = this;
            this.$refs[formName].validate(valid => {
                if (valid) {
                    // 将修改的信息提交到后台，保存在个人信息
                    ChangMessage(
                        this.$refs.nickname.value,
                        this.$refs.phonenumber.value
                    ).then(res => {
                        if (res.data.code == "0") {
                            that.open1();
                            // 拿到修改后返回的个人信息
                            Mymessage().then(res => {
                                that.mymessageObj = res.data.data;
                            });
                            // 清空修改信息的输入框
                            (that.ruleForm.nickname = " "),
                                (that.ruleForm.phonenumber = " ");
                        } else {
                            return false;
                        }
                    });
                } else {
                    return false;
                }
            });
        },
        open1 () {
            this.$message({
                showClose: true,
                message: "修改成功！"
            });
        },
        // 退出登录
        myClose () {
            goOut();
            this.$router.push("/login");
        }
    },

    created () {
        // 拿到注册后返回的个人信息
        Mymessage().then(res => {
            this.mymessageObj = res.data.data;
            this.touxiang = res.data.data.avatar;
        });
        // 拿到登录后返回的收藏列表
        CollectList().then(res => {
            this.collectList = res.data.rows;
            // console.log(res);
        });
    },
    watch: {
        selected (newValue) {
            if (newValue == "index") {
                this.$router.push("/index");
            } else if (newValue == "my") {
                this.$router.push("/my");
            } else if (newValue == 'music') {
                this.$router.push('/music');
            }
        }
    }
};
</script>
