import Vue from 'vue'
import Router from 'vue-router'
// 登陆
import Login from './views/Login.vue'
// 注册
import Register from './views/Register.vue'
// 忘记密码
import Forget from './views/Forget.vue'
// 栏目条
import Section from './views/Section.vue'

// 搜索页
import Searchpage from './views/Search.vue'
import Searchhot from './components/search/searchhot.vue'
import Searchlist from './components/search/searchlist.vue'
import Searchcards from './components/search/searchcards.vue'
// 首页

import Index from './views/Index.vue'
// 首页中的搜索框
import Search from './components/index/search.vue'
// 首页轮播图
import Swipe from './components/index/swipe.vue'
// 首页新闻卡片
import Cards from './components/index/cards.vue'

// 首页栏目条组件
import SectionBar from './components/index/sectionbar.vue'

// 详情
import Detail from './views/Detail.vue'

// 我的
import My from './views/My.vue'

import Demo from './views/demo.vue'

// 音乐
import Music from './views/Music.vue'

Vue.use(Router)

export default new Router({
    routes: [{
            path: '/',
            // name: 'forget',
            redirect: 'demo'
        },
        // 登录
        {
            path: '/login',
            // name: 'login',
            component: Login
        },
        // 注册
        {
            path: '/register',
            // name: 'register',
            component: Register
        },
        // 忘记密码
        {
            path: '/forget',
            // name: 'register',
            component: Forget
        },
        // 栏目
        {
            path: '/section',
            // name: 'register',
            component: Section
        },
        // 我的
        {
            path: '/my',
            component: My
        },
        // 搜索
        {
            path: '/searchpage',
            component: Searchpage,
            children: [{
                    path: '',
                    components: {
                        Search: Search,
                        Searchhot: Searchhot,
                    }

                },
                {
                    path: 'searchResult',
                    components: {
                        Search: Search,
                        Searchhot: Searchlist
                    },
                    children: [{
                        path: '',
                        components: {
                            Searchcards
                        }
                    }]

                }
            ]
        },
        {
            // 要在首页上显示搜索框，配置同一个地址对应多个路由
            path: '/index',
            // name: 'index',
            component: Index,
            children: [{
                path: '',
                components: {
                    Search: Search,
                    Swipe: Swipe,
                    Cards: Cards,
                    SectionBar: SectionBar
                }
            }]
        },
        {
            path: '/detail/:id',
            component: Detail
        },
        {
            path: '/demo',
            component: Demo
        },
        {
            path: '/music',
            component: Music
        }
    ]
})