<template>
	<div class="demo">
		<h1>{{testText}}</h1>
		<transition enter-active-class="animated rollIn">
			<el-button round v-show="flag" class="demo-btn" @click="go()">点击进入</el-button>
		</transition>
		
	</div>
</template>
<script>
export default {
	data() {
		return {
			nowIndex: 0,
			testText: "愿给您带来",
			flag: false
		};
	},
	methods:{
		go(){
			this.$router.push('/login')
		}
	},
	mounted() {
		let that = this;
		let timer = setInterval(function() {
			that.nowIndex++;
			switch (that.nowIndex) {
				case 1:
					that.testText = "极简视听体验";
					break;
				case 2:
					that.testText = "让我们一起";
					break;
				case 3:
					that.testText = "知晓世界";
					break;
			}
			if (that.nowIndex > 3) {
				that.flag = true;
				setTimeout(() => {
					clearInterval(timer);
				}, 2000);
			}
		}, 2000);
	}
};
</script>
<style lang="less">
.demo {
	height: 100vh;
	background: #409eff;
	overflow: hidden;
	h1 {
		color: white;
		margin-top: 30vh;
		text-align: center;
		font-size: 34px;
		// line-height: 1;
		// 设置动画效果
		// alternate动画先正常运行再反方向运行，并持续交替运行
		// ease-in-out由慢到快再到慢。
		animation: letterspacing 1s 7 alternate ease-in-out;
		display: block;
		// 设置文字间的间距
		letter-spacing: 5px;
	}
	.demo-btn {
		display: block;
		margin: 26vh auto;
		color: #888;
		font-size: 16px;
	}
	@keyframes letterspacing {
		0% {
			// 使文字缩成一团
			letter-spacing: -72px;
			// 为字体设置高斯模糊效果
			filter: blur(20px);
		}

		40% {
			filter: blur(6px);
		}

		80% {
			letter-spacing: 8px;
			filter: blur(0);
		}
	}
	el-button {
		display: none;
	}
}
</style>