<template>
    <div>
        <!-- 顶栏 -->
        <mt-header :title="title">
            <!-- <span @click="back" slot="left">
                <mt-button icon="back">返回</mt-button>
            </span>-->
            <!-- <mt-button icon="more" slot="right"></mt-button> -->
        </mt-header>
        <!-- 中间的卡片 -->
        <div class="music-main">
            <li
                class="music-card"
                v-for="(item, index) in musicList"
                :key="item.id"
                @click="play(item.id, index)"
                ref="cardli"
            >
                <img :src="item.al.picUrl" alt />
                <div class="music-info">
                    <span class="music-title">{{item.name}}</span>
                    <p class="music-author">{{item.al.name}}</p>
                    <!-- 暂停的时候显示的按钮 -->
                    <!-- 没选中或者选中没播放 -->
                    <div
                        v-show="selectedIndex !== index || isPause"
                        class="el-icon-video-play play-btn"
                        @click.prevent="control"
                    ></div>
                    <!-- 播放的时候显示的按钮 -->
                    <!-- 选中且播放 -->
                    <div
                        v-show="selectedIndex == index && !isPause"
                        class="el-icon-video-pause play-btn"
                        @click.prevent="control"
                    ></div>
                    <div class="num">{{index + 1}}</div>
                </div>
            </li>
        </div>

        <!-- 底部的播放控制 -->
        <div class="music-bottom">
            <div class="music-bot-title">{{name}}</div>
            <audio ref="myAudio" controls src></audio>
            <div class="music-pre el-icon-d-arrow-left" @click="pre"></div>
            <!-- <div class="music-play el-icon-video-play el-icon-video-pause" @click="control"></div> -->
            <div
                :class="{'music-play':true, 'el-icon-video-play': isPause, 'el-icon-video-pause': !isPause}"
                @click="control"
            ></div>
            <div class="music-next el-icon-d-arrow-right" @click="next"></div>
        </div>

        <!-- 显示底部tabbar -->
        <mt-tabbar v-model="selected">
            <mt-tab-item id="index">
                <span slot="icon" class="iconfont icon-news_icon"></span>
                首页
            </mt-tab-item>

            <mt-tab-item id="music">
                <!-- <img slot="icon" src="../assets/100x100.png" /> -->
                <span slot="icon" class="iconfont icon-yinle"></span>
                音乐
            </mt-tab-item>

            <mt-tab-item id="my">
                <span slot="icon" class="iconfont icon-wode"></span>
                我的
            </mt-tab-item>
        </mt-tabbar>
    </div>
</template>

<script>
import {
    getHotList,
    getUrl
} from '../services/music'

export default {
    data () {
        return {
            selected: 'music',
            title: '音乐排行榜',
            musicList: [],
            isPause: true,
            currentIndex: 1,
            nowId: 0,
            selectedIndex: null,
            name: '网易云'
        }
    },
    created () {
        getHotList().then(res => {
            this.musicList = res.data.playlist.tracks.slice(0, 50);
            // console.log(this.musicList)
        })
    },
    methods: {
        play (id, index) {
            // 将选中的index保存起来
            this.selectedIndex = index;
            // 获取现在和之前的li
            var currentLi = this.$refs.cardli[index];
            var preLi = this.$refs.cardli[this.currentIndex];

            // 只有在两次点击的对象不是同一个的时候才执行
            if (this.currentIndex != index) {
                currentLi.setAttribute('class', 'music-card music-active');
                preLi.setAttribute('class', 'music-card');
                // 将当前点击对象保存起来
                this.currentIndex = index;
                this.isPause = false;
            }
            // 只有在请求id不一样的时候才执行 防止重新开始播放
            if (this.nowId != id) {
                this.setUrl(id);
                // this.nowId = id
            }
        },
        setUrl (id) {
            this.nowId = id;
            getUrl(id).then(res => {
                var Audio = this.$refs.myAudio;
                Audio.src = res.data.data[0].url;
                Audio.play();
                this.isPause = false;
            })
        },
        control () {
            // 控制播放暂停
            var Audio = this.$refs.myAudio;
            if (Audio != null) {
                if (Audio.paused) {
                    Audio.play();
                    this.isPause = false;
                } else {
                    Audio.pause();
                    this.isPause = true;

                }
            }
        },
        pre () {
            if (this.currentIndex > 0) {
                var preLi = this.$refs.cardli[this.currentIndex];
                preLi.setAttribute('class', 'music-card');

                this.selectedIndex -= 1
                this.currentIndex--;

                var curLi = this.$refs.cardli[this.currentIndex];
                curLi.setAttribute('class', 'music-card music-active');
                // this.play(this.musicList[this.currentIndex].id, this.currentIndex);
                this.setUrl(this.musicList[this.currentIndex].id)
            }
        },
        next () {
            if (this.currentIndex < 99) {
                var preLi = this.$refs.cardli[this.currentIndex];
                preLi.setAttribute('class', 'music-card');

                this.selectedIndex += 1

                this.currentIndex++;

                var curLi = this.$refs.cardli[this.currentIndex];
                curLi.setAttribute('class', 'music-card music-active');

                this.setUrl(this.musicList[this.currentIndex].id)
            }
        }

    },
    watch: {
        nowId () {
            this.name = this.musicList[this.currentIndex].name;
        },
        selected (newValue) {
            if (newValue == 'index') {
                this.$router.push('/index');
            } else if (newValue == 'my') {
                this.$router.push('/my');
            } else if (newValue == 'music') {
                this.$router.push('/music');
            }
        }
    }
}
</script>

