<template>
    <div class="block">
        <!-- <span class="demonstration">Click 指示器触发</span> -->
        <el-carousel trigger="click" height="35vh">
            <el-carousel-item v-for="item in swipeArr" :key="item.articleId">
                <!-- <h3 class="small">{{ item }}</h3> -->
                <img :src="item.bannerImgUrl" alt @click="goDetail(item.articleId)" />
            </el-carousel-item>
        </el-carousel>
    </div>
</template>

<script>
// 引入请求轮播图的方法
import { getSwipe } from '../../services/swipe'

export default {
    data () {
        return {
            swipeArr: []
        }
    },
    methods: {
        goDetail (articleId) {
            this.$router.push(`/detail/${articleId}`);
        }
    },
    created () {
        getSwipe().then(res => {
            this.swipeArr = res.data.rows;
        })
    }
}
</script>

<style lang="less">
.el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 150px;
    text-align: center;
    margin: 0;
}

// 设置图片大小
.el-carousel__item img {
    width: 100%;
    height: 100%;
}

.el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n + 1) {
    background-color: #d3dce6;
}
</style>