import http from './api'

// 获取个人信息
function Mymessage() {

    return http.get('/api/login-user/info');
}

// 修改个人信息
function ChangMessage(userName, phonenumber) {
    let formdata = new FormData()
    formdata.append('userName', `${userName}`)
    formdata.append('phonenumber', `${phonenumber}`)

    return http.post('/api/system/user/profile/update', formdata)
}

// 修改头像
function SetAvatar(avatarfile) {
    let formdata = new FormData()
    formdata.append('avatarfile', avatarfile)
    return http.post('/api/system/user/profile/update/avatar/nos', formdata)
}

// 退出登录
function goOut(){
    return http.get('/api/logout')
}

export {
    Mymessage,
    ChangMessage,
    SetAvatar,
    goOut
}