import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

// 完整引入elementUI
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
Vue.use(ElementUI)

//引入MintUI
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
Vue.use(MintUI)

// 引入tabfooter图标的样式文件
import './assets/tabfooter/iconfont.css'
// 引入more字体图标
import './assets/indexmore/iconfont.css'
// 引入评论字体图标
import './assets/comment/iconfont.css'
import './assets/comment-active/iconfont.css'
// 引入搜索图标
import './assets/search/iconfont.css'
// 引入music图标
import './assets/music/iconfont.css'

// 引入Vue-axios
import axios from 'axios'
import Vueaxios from 'vue-axios'
Vue.use(Vueaxios, axios)

// 引入动画库
import animated from "animate.css";
Vue.use(animated)
Vue.config.productionTip = false

new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app')