<template>
    <div>
        <!-- 顶栏 -->
        <mt-header :title="title">
            <span @click="back" slot="left">
                <mt-button icon="back">返回</mt-button>
            </span>
            <!-- <mt-button icon="more" slot="right"></mt-button> -->
        </mt-header>

        <!-- 新闻主体部分 -->
        <div class="main-news">
            <h2>{{newsContent.title}}</h2>
            <div class="contentBody" v-html="newsContent.content"></div>
        </div>

        <!-- 低栏 -->
        <!-- <mt-search v-model="value" cancel-text="取消" placeholder="搜索"></mt-search> -->

        <div id="footer">
            <!-- <div class="comment">发表评论</div> -->
            <div
                class="comment"
            >阅读量 {{newsContent.readNum}} &nbsp;&nbsp;&nbsp;点赞数 {{newsContent.zan}}</div>
            <!-- 评论消息图标 -->
            <!-- <div class="message iconfont icon-xiaoxi"></div> -->
            <div class="message"></div>
            <!-- <div class="shoucang iconfont icon-shoucang2"></div>
            <div class="dianzan iconfont icon-dianzan2"></div>-->

            <div
                :class="{shoucang:true, iconfont:true, 'icon-shoucang2':shoucangFlag, 'icon-shoucang': !shoucangFlag}"
                @click="changeShouCang"
            ></div>
            <div
                :class="{dianzan:true, iconfont:true, 'icon-dianzan2':dianzanFlag, 'icon-dianzan': !dianzanFlag}"
                @click="changeDianZan"
            ></div>
        </div>
    </div>
</template>

<script>
// 引入请求新闻详情的http模块
import { getDetail } from '../services/detail'
// 引入增加阅读量模块
import { addReadNum } from '../services/addReadNum'
// 引入收藏请求
import { shoucang } from '../services/shoucang'
// 引入点赞请求
import { DianZan } from '../services/dianzan'

export default {
    data () {
        return {
            title: '',
            value: '',
            shoucangFlag: false,
            dianzanFlag: false,
            newsContent: ''
        }
    },
    methods: {
        /**
         * 改变点赞收藏的状态
         */
        changeShouCang () {
            var id = this.$route.params.id;
            shoucang(this.$route.params.id).then(() => {
                this.shoucangFlag = !this.shoucangFlag;

                getDetail(id).then(res => {
                    // console.log(res.data.data);
                    this.newsContent = res.data.data;
                });
            })
        },
        changeDianZan () {
            var id = this.$route.params.id;
            // 如果当前为选中状态，直接取消选中
            if (this.dianzanFlag) {
                this.dianzanFlag = !this.dianzanFlag;
            } else {
                // 如果当前没有选中则发送请求
                DianZan(this.$route.params.id).then(() => {
                    this.dianzanFlag = !this.dianzanFlag;
                    // console.log(res)

                    getDetail(id).then(res => {
                        // console.log(res.data.data);
                        this.newsContent = res.data.data;
                    });
                })
            }

        },

        // 返回上一页
        back () {
            this.$router.go(-1);
        }
    },
    created () {
        var id = this.$route.params.id;
        var that = this;

        // 获取新闻详情
        getDetail(id).then(res => {
            // console.log(res.data.data);
            this.newsContent = res.data.data;

            // 增加阅读量
            addReadNum(id)

            /**
         * 判断是否收藏和点赞
         */

            if (that.newsContent.isCollect) {
                that.shoucangFlag = true;

            } else {
                that.shoucangFlag = false;
            }
        });
    },
    updated () {
        // var id = this.$route.params.id;
        // getDetail(id).then(res => {
        //     // console.log(res.data.data);
        //     this.newsContent = res.data.data;
        // });
    }
}
</script>

