<template>
    <div class="outer">
        <div class="search-title" v-show="isSearchPage">知晓</div>
        <div class="search-back" v-show="!isSearchPage" @click="searchback">取消</div>
        <div class="search-box">
            <div class="search d2">
                <div class="div-form">
                    <input
                        type="text"
                        placeholder="知晓从这里开始..."
                        ref="input"
                        @focus="goSearch"
                        @keyup.enter="setHistory($event)"
                    />
                    <button type="button" @click="setHistory($event)" class="iconfont icon-sousuo"></button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data () {
        return {
            isSearchPage: false
        }
    },
    methods: {
        setHistory (e) {
            if (this.$refs.input.value != "") {
                // 搜索页加载时候已经创建了localstorage，在此直接判断重复即可
                // 获取input值
                var currentInputValue = this.$refs.input.value;
                // 设置个标志 看当前本地记录中是否有该值
                var flag = false;
                // 获取本地记录中的内容
                var historyObj1 = localStorage.getItem("searchHistory");
                // 解析
                historyObj1 = JSON.parse(historyObj1);
                // 遍历本地内容
                historyObj1.arr.forEach(function (item) {
                    // 如果存在重复，将标志设为true
                    if (item == currentInputValue) {
                        flag = true;
                    }
                });
                // 如果没有重复值
                if (!flag) {
                    // 存入
                    historyObj1.arr.push(currentInputValue);
                }
                // 将本地内容存到store中
                this.$store.commit({
                    type: "history/changeHistoryList",
                    arr: historyObj1.arr
                });
                // 解析
                historyObj1 = JSON.stringify(historyObj1);
                // 新存入本地
                localStorage.setItem("searchHistory", historyObj1);

                // 判断：如果当前是这页就不push
                //this.$router.push('/searchpage/searchResult')
                if (this.$route.path == "/searchpage/searchResult") {
                    return false;
                } else {
                    this.$router.push("/searchpage/searchResult");
                }
            } else {
                alert("输入内容为空");
            }

            // 根据输入框中的值向后台请求数据---搜索
            this.$store.dispatch("resultList/setSearchList", {
                title: this.$refs.input.value
            });

            e.target.blur();
        },
        goSearch () {
            this.$router.push('/searchPage')
        },
        searchback () {
            this.$router.go(-1);
        }
    },
    created () {
        if (this.$route.path == '/index') {
            this.isSearchPage = true;
        } else {
            this.isSearchPage = false;
        }
    }
};

</script>

