// 栏目
<template>
    <div class="section">
        <mt-header title="所有栏目">
            <router-link to="/" slot="left">
                <mt-button @click="handleClose">关闭</mt-button>
            </router-link>
        </mt-header>
        <!-- 我的栏目 -->
        <div class="my-section">
            <h4>我的栏目</h4>
            <transition-group appear tag="div" class="my-section-in">
                <li v-for="(item,index) in top" :key="index">
                    {{item.name}}
                    <i class="el-icon-close close" @click="sectionClose(item,index)"></i>
                </li>
            </transition-group>
        </div>
        <!-- 更多栏目 -->
        <div class="more-section">
            <h4>更多栏目</h4>
            <transition-group appear tag="div">
                <li
                    v-for="(item,index) in bottom"
                    :key="index"
                    @click="toTop(item,index)"
                >{{item.name}}</li>
            </transition-group>
        </div>
    </div>
</template>
<script>
import { sectionList, getDinyue, Dinyue } from '../services/section'
export default {
    data () {
        return {
            top: [],
            bottom: []
        };
    },
    methods: {
        // 下方点击去上方
        toTop (item, index) {
            this.top.push(item);
            this.bottom.splice(index, 1);
        },
        // 点击关闭按钮执行的方法
        handleClose () {
            /**
             * 点击关闭按钮的时候，首先遍历top数组，将id取出来
             * 调用向后台发送订阅思信息的接口
             */

            var ids = '';
            this.top.forEach(function (item) {
                ids += item.categoryId + ',';
            })

            Dinyue(ids)
            this.$router.go(-1);
        },
        // 上方点击删除按钮
        sectionClose (item, index) {
            this.bottom.push(item);
            this.top.splice(index, 1)
        },
        // 上方点击进入对应的新闻列表
    },
    created () {
        var that = this;
        // 获取顶部的已经订阅的列表
        getDinyue().then(res => {
            this.top = res.data.rows;
            // console.log(that.top[0].categoryId)
            // that.$store.state.indexNewsList.currentCategoryId = that.top[0].categoryId ? that.top[0].categoryId : 1;

            // 显示底部列表
            sectionList().then(res => {
                this.bottom = res.data.rows;

                // 处理底部的数据，如果上边已将存在，下边则不在显示
                for (var i = 0; i < this.top.length; i++) {
                    for (var j = 0; j < this.bottom.length; j++) {
                        if (this.top[i].categoryId == this.bottom[j].categoryId) {
                            this.bottom.splice(j, 1);
                            j--;
                        }
                    }
                }
            });
        })

    }
};
</script>
<style lang="less">
body {
    margin: 0;
}
.section {
    height: calc(100vh - 50px);
    width: 100%;
    overflow: hidden;
    // 顶栏
    .mint-header {
        background-color: #409eff !important;
    }

    li {
        list-style: none;
        width: 18%;
        height: 24px;
        line-height: 24px;
        display: inline-block;
        text-align: center;
        padding: 0 5px;
        margin: 5px 5px;
        border: 1px solid #ccc;
        border-radius: 25px;
        font-size: 15px;
    }
    // 我的栏目
    .my-section {
        height: 30vh;
        padding: 0 5px;

        .my-section-in {
            overflow-y: scroll;
            height: 66%;
        }

        width: 100%;
        overflow: hidden;
        li {
            background-color: #60acf9;
            color: white;
            border: 1px solid #60acf9;
        }
        .close {
            color: rgb(255, 255, 255) !important;
        }
    }
    // 更多栏目
    .more-section {
        height: 50vh;
        padding: 0 5px;

        width: 100%;
        overflow: hidden;
    }
    // 动画效果
    .v-enter,
    .v-leave-to {
        opacity: 0;
    }
    .v-enter-active,
    .v-leave-active {
        transition: all 0.6s ease;
    }
}
</style>