<template>
    <div>
        <!-- 显示顶部的搜索框 -->
        <router-view name="Search"></router-view>

        <!-- 显示主体的部分 -->
        <div class="main">
            <!-- 显示栏目条 -->
            <router-view name="SectionBar"></router-view>

            <div class="main-in-box" ref="mianInBox">
                <router-view name="Swipe"></router-view>
                <!-- 显示新闻卡片部分 -->
                <router-view name="Cards"></router-view>
            </div>
        </div>

        <!-- 显示底部tabbar -->
        <mt-tabbar v-model="selected">
            <mt-tab-item id="index">
                <!-- <img slot="icon" src="../assets/100x100.png" /> -->
                <span slot="icon" class="iconfont icon-news_icon"></span>
                首页
            </mt-tab-item>

            <mt-tab-item id="music">
                <!-- <img slot="icon" src="../assets/100x100.png" /> -->
                <span slot="icon" class="iconfont icon-yinle"></span>
                音乐
            </mt-tab-item>

            <mt-tab-item id="my">
                <!-- <img slot="icon" src="../assets/100x100.png" /> -->
                <span slot="icon" class="iconfont icon-wode"></span>
                我的
            </mt-tab-item>
        </mt-tabbar>
    </div>
</template>

<script>
export default {
    data () {
        return {
            selected: 'index',
            scrollTop: 0
        }
    },
    watch: {
        selected (newValue) {
            if (newValue == 'index') {
                this.$router.push('/index');
            } else if (newValue == 'my') {
                this.$router.push('/my');
            } else if (newValue == 'music') {
                this.$router.push('/music');
            }
        }
    },
    created () {

    }

}
</script>

