<template>
    <div>
        <mt-loadmore
            :top-method="loadTop"
            :bottom-method="loadBottom"
            :bottom-all-loaded="allLoaded"
            :autoFill="false"
            ref="loadmore"
        >
            <ul class="card">
                <li
                    v-for="(item, index) in NewsList"
                    :key="index"
                    @click="goDetail(item.articleId)"
                >
                    <div class="left">
                        <img :src="item.coverImgUrl" alt />
                    </div>
                    <div class="right">
                        <!-- 新闻标题 -->
                        <p class="title">{{item.title}}</p>
                        <div class="author-time-box">
                            <!-- 文章来源 -->
                            <p class="author">{{item.articleSource}}</p>
                            <!-- 时间 -->
                            <p class="time">{{item.createTime}}</p>
                        </div>
                    </div>
                </li>
            </ul>
        </mt-loadmore>
    </div>
</template>

<script>
// 引入获取订阅栏目接口
import { getDinyue } from '../../services/section'
export default {
    data () {
        return {
            // 是否加载完毕的标志
            allLoaded: false,
            pageNum: 1,
            pageSize: 10
        }
    },
    methods: {
        // 带着文章的id，前往详情页
        goDetail (articleId) {
            this.$router.push(`/detail/${articleId}`);
        },
        // 刷新
        loadTop () {
            // 将页数置1，重新请求数据
            this.pageNum = 1;
            this.pageSize = 10;
            // 刷新的时候根据当前的分类id
            var currentCategoryId = this.$store.state.indexNewsList.currentCategoryId;
            this.$store.dispatch('indexNewsList/setNewsList', {
                pageNum: this.pageNum,
                pageSize: this.pageSize,
                categoryId: currentCategoryId
            })

            this.$refs.loadmore.onTopLoaded();
        },
        // 加载更多
        loadBottom () {
            // 判断当前数据个数，是否大于新闻总量，如果大于
            // 将加载完毕标志置true，否则再请求数据
            if (this.$store.state.indexNewsList.newsListArr.length >= this.$store.state.indexNewsList.total) {
                this.allLoaded = true;
            } else {
                // 请求翻页
                this.pageNum++;
                // 加载更多
                this.getMore();
            }
            this.$refs.loadmore.onBottomLoaded();
        },
        // 获取更多数据
        getMore () {
            this.$store.dispatch('indexNewsList/moreNewsList', {
                pageNum: this.pageNum,
                pageSize: this.pageSize
            })
        }
    },
    computed: {
        NewsList () {
            // 如果store中的数据发生变化的时候，根据选项卡的选择实时的变化
            return this.$store.state.indexNewsList.newsListArr;
        }
    },
    created () {
        var defaultId = 1;
        getDinyue().then(res => {
            if (res.data.rows.length == 0) {
                // 如果订阅的栏目为0的话，默认显示第一个id的
                defaultId = 1;
            } else {
                defaultId = res.data.rows[0].categoryId;
            }

            // 请求完栏目以后，刷新数据
            this.$store.dispatch('indexNewsList/setNewsList', {
                pageNum: this.pageNum,
                pageSize: this.pageSize,
                categoryId: defaultId
            })
        })
        // // 判断页面数据是否存在，如果存在则不再向后台请求
        // if (this.$store.state.indexNewsList.newsListArr.length == 0) {
        //     // 默认加载1头条新闻
        //     this.$store.dispatch('indexNewsList/setNewsList', {
        //         pageNum: this.pageNum,
        //         pageSize: this.pageSize,
        //         categoryId: defaultId
        //     })
        // }

    }

}
</script>

