        padding: 4px;

        .left {
            width: 38%;
            height: 100%;
            transform: scale(0.9);
            // background-color: #8822ff;
            border-radius: 3px;
            float: left;
            overflow: hidden;

            img {
                width: 100%;
                height: 100%;
            }
        }

        .right {
            width: 59%;
            height: 100%;
            // background-color: #22ff64;
            float: left;
            margin-left: 7px;
            box-sizing: border-box;
            padding-left: 5px;
            position: relative;

            .title {
                font-size: 14px;
                margin: 5px 0 5px;
                max-height: calc(100% - 50px);
