            }

            .author-time-box {
                position: absolute;
                bottom: 4px;
                color: rgb(131, 131, 131);

                .author {
                    font-size: 12px;
                    margin: 0;
                }
                .time {
                    margin: 0;
                    font-size: 12px;
                }
            }
        }
    }
}
</style>