// 搜索历史与热门搜索
<template>
    <div class="search-middle">
        <div class="search-history">
            <p>搜索历史</p>
            <li
                class="searchHistoryLi"
                v-for="(item,index) in historyArr"
                :key="index"
                @click="goResult($event)"
            >{{item}}</li>
        </div>
        <div class="search-hot">
            <i>知晓热门</i>
            <ul>
                <li
                    v-for="(item,index) in hot"
                    :key="index"
                    ref="hotlist"
                    @click="goDetail(item.articleId)"
                >
                    <mt-badge size="small" type="primary" class="number">{{index+1}}</mt-badge>
                    {{item.title}}
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
import { getHotList } from "../../services/indexlist";
// import { getDetail } from "../../services/detail";
export default {
    data () {
        return {
            hot: []
        };
    },
    methods: {
        goDetail (articleId) {
            this.$router.push(`/detail/${articleId}`);
        },
        // 点击搜索历史进入搜索结果页
        goResult (e) {
            // 根据输入框中的值向后台请求数据---搜索
            this.$store.dispatch("resultList/setSearchList", {
                title: e.target.innerText
            });
            this.$router.push("/searchpage/searchResult");
        }
    },
    computed: {
        historyArr () {
            return this.$store.state.history.historyList;
        }
    },
    created () {
        getHotList().then(res => {
            this.hot = res.data.rows;
            // console.log(res.data.rows);
        });
        // 先取值，取不到再新建
        // 新建
        if (!localStorage.getItem("searchHistory")) {
            var historyObj = {
                arr: []
            };
            this.$store.commit({
                type: "history/changeHistoryList",
                arr: historyObj.arr
            });

            historyObj = JSON.stringify(historyObj);
            localStorage.setItem("searchHistory", historyObj);
        } else {
            // 取值
            var historyObj1 = localStorage.getItem("searchHistory");
            historyObj1 = JSON.parse(historyObj1);

            this.$store.commit({
                type: "history/changeHistoryList",
                arr: historyObj1.arr
            });
        }
    },
    // 修改热门搜索中的前1，2，3位的样式
    updated () {
        for (var i = 0; i < this.$refs.hotlist.length; i++) {
            var innerText = this.$refs.hotlist[i].childNodes[0].innerText;
            if (innerText == "1" || innerText == "2" || innerText == "3") {
                this.$refs.hotlist[i].childNodes[0].style.cssText =
                    "font-size:16x;font-weight:bolder;font-style:italic";
            }
        }
    }
};
</script>
<style lang="less">
.search-middle {
    height: 100%;
    overflow: hidden;
    // 搜索历史
    .search-history {
        height: 20vh;
        color: #ccc;
        overflow-y: scroll;
        margin-bottom: 15px;
        p {
            padding-left: 5px;
        }

        li {
            list-style: none;
            width: 22%;
            height: 24px;
            line-height: 24px;
            display: inline-block;
            text-align: center;
            padding: 0 5px;
            margin: 5px 5px;
            border: 1px solid #ccc;
            border-radius: 10px;
        }

        .searchHistoryLi {
            overflow: hidden;
        }
    }
    // 热门搜索
    .search-hot {
        i {
            font-size: 20px;
            color: #409eff;
            margin-left: 5px;
        }
        ul {
            padding: 0 0 0 5px;
        }
        li {
            width: 97%;
            height: 25px;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            padding: 6px;
            color: #888;
            .number {
                display: inline-block;
                width: 15px;
                height: 16px;
                line-height: 16px;
                text-align: center;
            }
        }
        li:hover {
            color: #409eff;
            cursor: pointer;
        }
    }
}
</style>