<template>
    <div class="big-box">
        <div class="section-bar-box">
            <!-- 更过按钮图标 -->
            <div class="more iconfont icon-zhenhuichongtubiaozhizuo-kuozhan-" @click="goMore"></div>
            <ul class="section-bar" ref="sectionBarRef" :style="styleObj">
                <li
                    v-for="(item, index) in sectionList"
                    :key="index"
                    class="section-li"
                    @click="addStyle($event, item.categoryId)"
                    ref="allLi"
                >{{item.name}}</li>
            </ul>
        </div>
    </div>
</template>

<script>
// 引入获取已经订阅的列表接口
import { getDinyue } from '../../services/section'

export default {
    data () {
        return {
            sectionList: [''],
            // 用来设置ul宽度样式的对象
            styleObj: {
                width: ''
            }
        }
    },
    methods: {
        addStyle (e, id) {
            // console.log(e.target.parentNode.children)
            /**
             * 首先获取当前点击li的兄弟节点，并将兄弟节点集合转换成数组
             * 然后将兄弟节点的样式设置成默认的样式
             */
            Array.from(e.target.parentNode.children).forEach(element => {
                element.style.color = '#555555';
                element.style.fontWeight = 'normal';
                // console.log(element)
            });

            /**
             * 将当前选择的li字体设置成black
             */
            e.target.style.color = 'black';
            e.target.style.fontWeight = 'bold';


            // 保存所点击的id
            this.$store.state.indexNewsList.currentCategoryId = id;

            this.$store.dispatch('indexNewsList/setNewsList', {
                pageNum: 1,
                pageSize: 10,
                categoryId: id
            })

        },
        // 跳转到section栏目页、
        goMore () {
            this.$router.push('/section');
        }
    },
    created () {
        var that = this;
        // 获取订阅的列表
        getDinyue().then(res => {
            that.sectionList = res.data.rows;
            // 如果用户没有订阅任何栏目，则显示推荐
            if (res.data.rows.length == 0) {
                that.sectionList = [{ name: '推荐' }]
            }
            // that.$store.state.indexNewsList.currentCategoryId = res.data.rows[0].categoryId || 1;
        })
    },
    mounted () {
        this.$refs.allLi[0].style.color = 'black';
        this.$refs.allLi[0].style.fontWeight = 'bold';
    },
    watch: {
        sectionList () {
            // 根据栏目的个数设置ul的宽度上
            this.styleObj.width = 39 * this.sectionList.length + 'px';
        }
    }
}
</script>

<style lang="less">
.big-box {
    position: absolute;
    width: 100%;
    z-index: 100;
    background-color: #fff;
    border-bottom: 1px solid #ccc;

    // 显示更多图标
    .more {
        height: 30px;
        width: 30px;
        background-color: #fff;
        opacity: 0.9;
        -webkit-box-shadow: -1px 0px 8px 1px #ccc;
        box-shadow: -1px 0px 8px 1px #ccc;
        position: absolute;
        left: calc(100vw - 30px);
        text-align: center;
        line-height: 30px;
    }
    // ul·
    .section-bar-box {
        overflow-x: scroll;
        // overflow-y: hidden;

        .section-bar {
            margin: 0;
            padding: 0 0 0 5px;
            height: 30px;

            li {
                list-style: none;
                float: left;
                // background-color: #4febd1;
                padding: 0 3px 0 5px;
                height: 29px;
                line-height: 30px;
                font-size: 14px;
                color: #555555;
            }
        }
    }
}
</style>